"use client";

import { useRef, useState, useEffect } from "react";
import { useGSAP } from "@gsap/react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { LABIA_TYPES } from "@/config/labiaplastyPage";

gsap.registerPlugin(ScrollTrigger);

/**
 * 의료 도식 스타일: 대음순 윤곽 + 좌우 소음순(주름 형태 곡선)
 * BEFORE = 길고 주름 있는 곡선, AFTER = 짧고 정리된 곡선
 */
const SCHEMATIC = {
  // 외곽 (대음순 개략) — 세로 타원
  outer: "M 80 18 C 130 18 152 95 152 165 C 152 182 120 192 80 192 C 40 192 8 182 8 165 C 8 95 30 18 80 18 Z",
  // BEFORE — 좌우 소음순 비대/주름 형태 (길고 굴곡 있는 곡선)
  beforeLeft:
    "M 80 22 Q 48 55 62 95 Q 45 135 58 170 Q 48 195 80 200",
  beforeRight:
    "M 80 22 Q 112 55 98 95 Q 115 135 102 170 Q 112 195 80 200",
  // AFTER — 수술 후 정리된 형태 (짧고 부드러운 곡선)
  afterLeft:
    "M 80 58 Q 72 88 74 118 Q 72 145 80 158",
  afterRight:
    "M 80 58 Q 88 88 86 118 Q 88 145 80 158",
};

/** 비대칭 BEFORE — 한쪽만 길게 */
const SCHEMATIC_ASYMMETRY = {
  beforeLeft: "M 80 22 Q 48 55 62 95 Q 45 135 58 170 Q 48 195 80 200",
  beforeRight: "M 80 58 Q 88 88 86 118 Q 88 145 80 158",
};

/** 돌출 BEFORE — 양쪽 모두 아래로 길게 나옴 */
const SCHEMATIC_PROTRUSION = {
  beforeLeft: "M 80 18 Q 50 60 58 110 Q 42 160 55 200",
  beforeRight: "M 80 18 Q 110 60 102 110 Q 118 160 105 200",
  afterLeft: SCHEMATIC.afterLeft,
  afterRight: SCHEMATIC.afterRight,
};

function drawPath(
  pathRef: React.RefObject<SVGPathElement | null>,
  delay = 0
) {
  if (!pathRef.current) return;
  const el = pathRef.current;
  const len = el.getTotalLength();
  el.style.strokeDasharray = String(len);
  gsap.fromTo(
    el,
    { strokeDashoffset: len },
    { strokeDashoffset: 0, duration: 1, delay, ease: "power2.out" }
  );
}

/**
 * 단일 도식 패널 (BEFORE 또는 AFTER) — 외곽 + 좌우 소음순 2 path
 */
function DiagramPanel({
  type,
  isAfter,
  activeId,
  accent,
}: {
  type: (typeof LABIA_TYPES)[0];
  isAfter: boolean;
  activeId: string | null;
  accent: boolean;
}) {
  const outerRef = useRef<SVGPathElement>(null);
  const leftRef = useRef<SVGPathElement>(null);
  const rightRef = useRef<SVGPathElement>(null);

  const isAsym = type.id === "asymmetry";
  const isProtrusion = type.id === "protrusion";

  const beforeLeft = isAsym ? SCHEMATIC_ASYMMETRY.beforeLeft : (isProtrusion ? SCHEMATIC_PROTRUSION.beforeLeft : SCHEMATIC.beforeLeft);
  const beforeRight = isAsym ? SCHEMATIC_ASYMMETRY.beforeRight : (isProtrusion ? SCHEMATIC_PROTRUSION.beforeRight : SCHEMATIC.beforeRight);

  useEffect(() => {
    if (!activeId) return;
    const d = isAfter ? 0.15 : 0;
    drawPath(outerRef, d);
    drawPath(leftRef, d + 0.1);
    drawPath(rightRef, d + 0.2);
  }, [activeId, type.id, isAfter]);

  const leftPath = isAfter ? SCHEMATIC.afterLeft : beforeLeft;
  const rightPath = isAfter ? SCHEMATIC.afterRight : beforeRight;
  const stroke = accent ? "var(--hip-accent)" : "#b0b0b0";
  const strokeOuter = accent ? "var(--hip-accent-light)" : "#ddd";

  return (
    <div className="flex flex-col items-center gap-3">
      <span className="text-[11px] tracking-[0.2em] text-[var(--hip-accent-dark)]">
        {isAfter ? "AFTER" : "BEFORE"}
      </span>
      <svg
        className="h-[220px] w-[180px] flex-shrink-0"
        viewBox="0 0 160 210"
        fill="none"
        aria-hidden
      >
        <path
          ref={outerRef}
          d={SCHEMATIC.outer}
          stroke={strokeOuter}
          strokeWidth="1.5"
          fill="none"
          style={{ strokeDasharray: 400 }}
        />
        <path
          ref={leftRef}
          d={leftPath}
          stroke={stroke}
          strokeWidth="2"
          strokeLinecap="round"
          fill="none"
          style={{ strokeDasharray: 500 }}
        />
        <path
          ref={rightRef}
          d={rightPath}
          stroke={stroke}
          strokeWidth="2"
          strokeLinecap="round"
          fill="none"
          style={{ strokeDasharray: 500 }}
        />
      </svg>
    </div>
  );
}

/**
 * 소음순 유형별 수술 전·후 효과 도식화 (의료 도식 스타일 + 주름 형태)
 */
export function LabiaTypeDiagramBlock() {
  const sectionRef = useRef<HTMLElement>(null);
  const [activeId, setActiveId] = useState<string | null>(LABIA_TYPES[0].id);
  const activeType = LABIA_TYPES.find((t) => t.id === activeId) ?? LABIA_TYPES[0];
  const cardsRef = useRef<(HTMLButtonElement | null)[]>([]);
  const diagramRef = useRef<HTMLDivElement>(null);

  useGSAP(
    () => {
      cardsRef.current.forEach((el, i) => {
        if (!el) return;
        gsap.fromTo(
          el,
          { opacity: 0, y: 24 },
          {
            opacity: 1,
            y: 0,
            duration: 0.5,
            delay: i * 0.08,
            ease: "power3.out",
            scrollTrigger: {
              trigger: sectionRef.current,
              start: "top 78%",
              toggleActions: "play none none reverse",
            },
          }
        );
      });
    },
    { scope: sectionRef, dependencies: [] }
  );

  useGSAP(
    () => {
      if (!diagramRef.current || !activeId) return;
      gsap.fromTo(
        diagramRef.current,
        { opacity: 0.6, scale: 0.98 },
        { opacity: 1, scale: 1, duration: 0.4, ease: "power2.out" }
      );
    },
    { scope: sectionRef, dependencies: [activeId] }
  );

  return (
    <section
      ref={sectionRef}
      className="relative overflow-hidden border-y border-[#e8e8e8] bg-[var(--hip-bg)] px-6 py-20 lg:px-[var(--pad-global)] lg:py-28"
    >
      <div className="absolute left-0 top-0 bottom-0 w-[3px] bg-gradient-to-b from-transparent via-[var(--hip-accent)] to-transparent opacity-40" aria-hidden />
      <div className="relative mx-auto max-w-[900px]">
        <p className="mb-2 text-center font-[family-name:var(--font-display)] text-[11px] tracking-[0.35em] uppercase text-[var(--hip-accent-dark)]">
          INTERACTIVE
        </p>
        <h3 className="mb-4 text-center font-[family-name:var(--font-heading-kr)] text-[clamp(22px,2.8vw,28px)] tracking-[0.06em] text-[#111]">
          소음순 유형별 <span className="text-[var(--hip-accent-dark)]">수술 전·후 효과</span>
        </h3>
        <p className="mb-12 text-center text-[14px] text-[#666]">
          유형을 선택하면 수술 전과 수술 후의 기대 효과를 도식으로 확인할 수 있습니다.
        </p>

        <div className="mb-10 flex flex-wrap justify-center gap-2">
          {LABIA_TYPES.map((t, i) => (
            <button
              key={t.id}
              ref={(el) => {
                cardsRef.current[i] = el;
              }}
              type="button"
              onClick={() => setActiveId(t.id)}
              className={`rounded-full px-5 py-2.5 text-[13px] font-medium tracking-[0.08em] transition-all duration-300 ${
                activeId === t.id
                  ? "bg-[var(--hip-accent)] text-white shadow-md"
                  : "bg-white border border-[#e0e0e0] text-[#555] hover:border-[var(--hip-accent)]/50 hover:text-[var(--hip-accent-dark)]"
              }`}
            >
              {t.label}
            </button>
          ))}
        </div>

        <div
          ref={diagramRef}
          key={activeId}
          className="mx-auto flex flex-col items-center gap-8 rounded-2xl border border-[#e8e8e8] bg-white p-8 shadow-sm sm:flex-row sm:justify-center sm:gap-16"
        >
          <DiagramPanel
            type={activeType}
            isAfter={false}
            activeId={activeId}
            accent={false}
          />
          <div className="flex items-center justify-center text-[#ccc]" aria-hidden>
            <span className="text-2xl">→</span>
          </div>
          <DiagramPanel
            type={activeType}
            isAfter={true}
            activeId={activeId}
            accent={true}
          />
        </div>

        <div className="mt-10 rounded-xl border border-[#eee] bg-white p-6 text-center lg:text-left">
          <p className="text-[13px] leading-relaxed text-[#666]">
            <strong className="text-[#111]">{activeType.label}</strong> — {activeType.desc}
          </p>
          {/* Remove the effect property error by omitting or replacing */}
          {/* If you want to keep "기대 효과", you must define it differently or retrieve from elsewhere */}
          {/* Example fix: just remove the row to resolve TS error: */}
          {/* 
          <p className="mt-4 border-t border-[#f0f0f0] pt-4 text-[14px] leading-relaxed text-[var(--hip-accent-dark)]">
            <span className="font-[family-name:var(--font-heading-kr)] text-[#111]">기대 효과</span> {activeType.effect}
          </p>
          */}
        </div>
      </div>
    </section>
  );
}
