"use client";

import Link from "next/link";
import { useRef } from "react";
import { useGSAP } from "@gsap/react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

/**
 * 소음순 성형 히어로 — 배경·도식 없음, 텍스트 + CTA만. 유형 인터랙션은 아래 LabiaTypeDiagramBlock에서.
 */
export function LabiaplastyHero() {
  const sectionRef = useRef<HTMLElement>(null);
  const textRef = useRef<HTMLDivElement>(null);

  useGSAP(
    () => {
      if (!sectionRef.current || !textRef.current) return;
      gsap.fromTo(
        textRef.current,
        { opacity: 0, y: 24 },
        { opacity: 1, y: 0, duration: 1, ease: "power3.out", delay: 0.2 }
      );
    },
    { scope: sectionRef, dependencies: [] }
  );

  return (
    <section
      ref={sectionRef}
      className="relative flex min-h-[75vh] items-end overflow-hidden bg-white lg:min-h-[85vh]"
      aria-label="소음순 성형"
    >
      <div className="absolute left-0 top-0 bottom-0 w-[3px] bg-gradient-to-b from-transparent via-[var(--hip-accent)] to-transparent opacity-50" aria-hidden />

      <div ref={textRef} className="relative z-10 w-full px-6 pb-20 pt-28 lg:px-[var(--pad-global)] lg:pb-28 lg:pt-36">
        <div className="mx-auto max-w-[720px]">
          <span className="mb-4 block font-[family-name:var(--font-display)] text-[11px] tracking-[0.4em] uppercase text-[var(--hip-accent-dark)]">
            WOMEN&apos;S AESTHETIC
          </span>
          <h1 className="font-[family-name:var(--font-philo)] text-[clamp(36px,5.5vw,56px)] font-light leading-[1.2] tracking-tight text-[#111]">
            소음순 성형
          </h1>
          <p className="mt-2 font-[family-name:var(--font-heading-kr)] text-[clamp(18px,2.2vw,22px)] tracking-[0.02em] text-[var(--hip-accent-dark)]">
            레이저 · 수술, 당신에게 맞는 방법
          </p>
          <p className="mt-6 max-w-[520px] text-[15px] leading-relaxed text-[#555] lg:text-[16px]">
            유형별 맞춤 설계, 여성 의료진 1:1 전담, 흉터 최소화 —
            <br className="hidden sm:block" />
            왜 오마쥬에서 소음순 수술을 받아야 하는지, 정확하고 설득력 있게 안내해 드립니다.
          </p>
          <div className="mt-10 flex flex-wrap gap-4">
            <Link
              href="/#curation"
              className="group inline-flex items-center gap-2 rounded-sm bg-[var(--hip-accent)] px-7 py-4 text-[14px] font-medium tracking-[0.12em] text-white transition-all duration-300 hover:bg-[var(--hip-accent-dark)] hover:shadow-[0_0_30px_rgba(184,160,120,0.4)]"
            >
              비밀 상담 문의
              <span className="transition-transform group-hover:translate-x-1">→</span>
            </Link>
            <Link
              href="/#map"
              className="inline-flex items-center rounded-sm border-2 border-[#111] bg-transparent px-7 py-4 text-[14px] font-medium tracking-[0.1em] text-[#111] transition-colors hover:border-[var(--hip-accent)] hover:bg-[var(--hip-bg)]"
            >
              오시는 길
            </Link>
            <a
              href="#labia-types"
              className="inline-flex items-center rounded-sm border border-[#ccc] bg-white px-7 py-4 text-[14px] font-medium tracking-[0.1em] text-[#555] transition-colors hover:border-[var(--hip-accent)] hover:text-[var(--hip-accent-dark)]"
            >
              유형별 도식 · 정상으로 정리되는 과정 보기
            </a>
          </div>
        </div>
      </div>
    </section>
  );
}
